0px 20px; \r\n  background-color: #f1c40f; \r\n  color: black; \r\n  text-decoration: none; \r\n  font-weight: bold; \r\n  border-radius: 5px;\r\n  transition: background-color 0.3s; \r\n\r\n  box-sizing: border-box;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n.hero-text .explore-now-cta:hover {\r\n  background-color: #f39c12; \r\n}\r\n\r\n.hero-image img{\r\n  width:100%;\r\n  height:100vh;\r\n  object-fit: cover;\r\n}\r\n\r\n/* HERO END */\r\n\r\n/* EXPLORE START */\r\n.explore {\r\n  text-align: center;\r\n}\r\n.explore-text {\r\n\r\n\r\n  margin-top: 50px;\r\n  cursor: pointer;\r\n\r\n  text-align: center;\r\n  font-size: 30px;\r\n  font-weight: 700;\r\n  text-decoration: underline; \r\n}\r\n\r\n.explore-content {\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr);\r\n  grid-gap: 10px;\r\n\r\n  justify-items: center; \r\n  align-items: center;\r\n  margin-top: 50px;\r\n}\r\n\r\n.explore-item {\r\n  position: relative;\r\n  width: 350px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 5px;\r\n  overflow: hidden;\r\n  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);\r\n  transition: transform 0.3s ease;\r\n  margin-bottom: 20px;\r\n  justify-items: center;\r\n\r\n  cursor: pointer;\r\n}\r\n\r\n.explore-item:hover {\r\n  transform: translateY(-5px);\r\n}\r\n\r\n.rating {\r\n  position: absolute;\r\n  top: 10px;\r\n  right: 10px;\r\n  padding: 5px 10px;\r\n  background-color: rgba(255, 255, 255, 0.8);\r\n  border-radius: 5px;\r\n  font-size: 20px;\r\n  font-weight: bold;\r\n  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.explore-item img {\r\n  width: 100%;\r\n  height: 200px;\r\n  object-fit: cover;\r\n}\r\n\r\n.explore-item .card{\r\n  padding: 8px;\r\n\r\n}\r\n\r\n.explore-item .card .details {\r\n  padding: 25px;\r\n}\r\n\r\n.explore-item .card .city {\r\n  font-size: 16px;\r\n  font-style: italic;\r\n\r\n}\r\n\r\n.explore-item .card h3.name {\r\n  font-size: 20px;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.explore-item a {\r\n  text-decoration: none;\r\n  color: #333; /* Opsional: menetapkan warna tautan yang sesuai */\r\n}\r\n\r\n.explore-item .card .description {\r\n  font-size: 16px;\r\n  margin-bottom: 0;\r\n  text-align: justify;\r\n\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 3;\r\n  -webkit-box-orient: vertical;\r\n  overflow: hidden;\r\n}\r\n\r\n\r\n/* EXPLORE END */\r\n\r\n\r\n/* FOOTER START */\r\n/* Footer */\r\n/* Footer */\r\nfooter {\r\n  background-color: #f1f1f1;\r\n  padding: 20px 0;\r\n}\r\n\r\n.container {\r\n  max-width: 1400px;\r\n  margin: 0 auto;\r\n  padding: 0 20px;\r\n}\r\n\r\n.footer-content {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.footer-navigation ul {\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\n.footer-navigation ul li {\r\n  display: inline;\r\n  margin-right: 20px;\r\n}\r\n\r\n.footer-navigation ul li:last-child {\r\n  margin-right: 0;\r\n}\r\n\r\n.footer-navigation ul li a {\r\n  text-decoration: none;\r\n  color: #333;\r\n\r\n  transition: color 0.3s;\r\n}\r\n\r\n.footer-navigation ul li a:hover {\r\n  color: #f1c40f;\r\n}\r\n\r\n.footer-text p {\r\n  font-size: 14px;\r\n}\r\n\r\n\r\n/* FOOTER END */\r\n\r\n\r\n/* DETAIL PAGE START */\r\n.kembali{\r\n  margin-bottom: 5px;\r\n}\r\n.kembali a {\r\n  text-decoration: none;\r\n  color: black; /* Atur warna teks tautan sesuai kebutuhan */\r\n  font-size: 18px;\r\n  line-height: 44px;\r\n\r\n  display: inline-block; \r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n.kembali a:hover {\r\n  color: #f39c12;\r\n  transition: color 0.3s;\r\n}\r\n\r\n.restaurant-info {\r\n  display: flex;\r\n}\r\n\r\n.restaurant-image {\r\n  flex: 1;\r\n  margin-right: 20px;\r\n}\r\n\r\n.restaurant-image img {\r\n  width: 100%;\r\n  height: auto;\r\n\r\n  border-radius: 5px;\r\n}\r\n\r\n.restaurant-details {\r\n  flex: 2;\r\n}\r\n\r\n.restaurant-menu {\r\n  display: flex;\r\n  margin-top: 20px;\r\n}\r\n\r\n.restaurant-description{\r\n  text-align: justify;\r\n}\r\n\r\n.food-menu,\r\n.drink-menu {\r\n  flex: 1;\r\n  margin-right: 20px;\r\n}\r\n/* Form review start */\r\n.add-review {\r\n  padding: 20px;\r\n}\r\n\r\n.add-review h2 {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.add-review label {\r\n  display: block;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n\r\n.add-review textarea {\r\n  width: 100%;\r\n  border: 1px solid #ccc;\r\n  border-radius: 5px;\r\n  margin-bottom: 10px;\r\n  height: 100px;\r\n}\r\n\r\n.add-review input[type=\"text\"] {\r\n  width: 100%;\r\n  border: 1px solid #ccc;\r\n  height: 44px;\r\n}\r\n\r\n.add-review button[type=\"submit\"] {\r\n  font-size: 16px;\r\n  width: 100px;\r\n  height: 44px;\r\n  background-color: #f1c40f;\r\n  color: white;\r\n  border: none;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n\r\n\r\n  display: block;\r\n  margin: 0 auto;\r\n}\r\n\r\n.add-review button[type=\"submit\"]:hover {\r\n  background-color: #d4ac0d; /* Lighter shade on hover */\r\n}\r\n\r\n/* Form review end */\r\n\r\n/* Customer review start */\r\n.customer-reviews {\r\n  margin-top: 20px;\r\n}\r\n\r\n.review-wrapper{\r\n  display: grid;\r\n  gap: 10px;\r\n  grid-template-columns: repeat(3, 1fr);\r\n}\r\n\r\n.review {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.customer-reviews {\r\n  padding: 20px;\r\n}\r\n\r\n.review-card {\r\n  border: 1px solid #ccc;\r\n  border-radius: 5px;\r\n  padding: 20px;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.review-header {\r\n  display: flex;\r\n  align-items: center;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.avatar {\r\n  width: 50px;\r\n  height: 50px;\r\n  border-radius: 50%;\r\n  margin-right: 15px;\r\n}\r\n\r\n.customer-info {\r\n  flex: 1;\r\n}\r\n\r\n.customer-name {\r\n  font-weight: bold;\r\n  margin: 0;\r\n}\r\n\r\n.rating {\r\n  margin: 0;\r\n}\r\n\r\n.review-text {\r\n  margin: 0;\r\n}\r\n/* Customer review start */\r\n\r\n\r\n/* DETAIL PAGE END */"],"sourceRoot":""}]);
